<template>
    <div>
        <div class="blog-post">
            <h2 class="blog-post-title">{{article.title}}</h2>
            <p class="blog-post-meta">{{article.created}} {{article.author}} <a href="#">{{article.category}}</a>  0  0</p>
            <div>{{article.content}}</div>
        </div><!-- /.blog-post -->

        <div class="post-comment">
            <h3></h3>
            <div class="post-comment-list">
                <h5 class="post-comment-email">2324234@qq.com</h5>
                <p class="post-comment-content">sdlfkjsdlfksdfkl</p>
            </div>

            <p class="alert alert-info"></p>

            <form class="post-comment-form">
                <h3></h3>
                <div class="form-group">
                    <label></label>
                    <input class="form-control" name="email" type="text" placeholder="..." />
                </div>
                <div class="form-group">
                    <label></label>
                    <textarea class="form-control" name="content"></textarea>
                </div>
                <button class="btn btn-primary"></button>
            </form>
        </div>

    </div>
</template>
<script>
export default{
    data(){
        return{
            article:{},
            id:''
        }
    },
    methods:{
        //
        getArticle(id){
            this.$http.get('/article/'+ id).then(function(res){
                this.article=eval('('+res.body+')');
            },function(res){
                alert(' '+ res.status);
            });
        }
    },
    created(){
        this.id=window.location.search.split('=')[1];
        this.getArticle(this.id);
    }
}
</script>
<style scoped src="../css/blog.css">

</style>



