<template>
    <div>
        <h2 class="sub-header"> </h2>
        <form class="form-inline form-filter">
            <div class="form-group">
                <label></label>
                <select class="form-control" name="Category">
                    <option>Javascript</option>
                </select>
            </div>
            <div class="form-group">
                <label></label>
                <select class="form-control" name="author">
                    <option>admin</option>
                </select>
            </div>
            <div class="form-group">
                <label></label>
                <input class="form-control" name="keyword" type="text" />
            </div>
            <button class="btn btn-info"></button>
        </form>
        <div class="table-responsive">
            <table class="table table-striped">
                <thead>
                <tr>
                    <th width="30%"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(article,index) in articles">
                    <td>{{article.title}}</td>
                    <td>{{article.category}}</td>
                    <td>{{article.author}}</td>
                    <td>{{article.created}}</td>
                    <td>0</td>
                    <td>3</td>
                    <td></td>
                    <td>
                        <router-link class="btn btn-sm btn-success" :to="{path:'/userArticle', query:{id:article._id}}"></router-link>
                        <router-link class="btn btn-sm btn-info" :to="{path:'/adminAddArticle', query:{id:article._id}}"></router-link>
                        <a href="javascript:;" class="btn btn-sm btn-danger" @click="deleteArticle(article._id, index)"></a>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <nav>
            <ul class="pagination">
                <li><a href="#" class="active">1</a></li>
                <li><a href="#">2</a></li>
            </ul>
        </nav>
    </div>
</template>
<style>

</style>
<script>
    export default{
        data(){
            return{
                articles:[],
            }
        },
        methods:{
            //
            getArticleList(){
                this.$http.get('/article').then(function(res){
                    this.articles=eval('('+ res.body +')');
                },function(res){
                    alert(' '+ res.status);
                });
            },
            //
            deleteArticle(id, index){
                this.$http.delete('/article/' + id).then(function(res){
                    if(res.status==200) this.articles.splice(index,1);
                }, function(res){
                    alert(' '+ res.status);
                });
            }
        },
        components:{

        },
        created(){
            this.getArticleList();
        }
    }
</script>



