<template>
    <div>
        <div class="post" v-for="(article, index) in articles">
            <h3 class="post-title">
                <router-link :to="{path:'/userArticle', query:{id:article._id}}">{{article.title}}</router-link>
            </h3>
            <div class="post-abstract">{{article.content}}</div>
            <div class="post-meta">
                <span class="post-created">:&nbsp; {{article.created}}</span>
                <span class="post-author">:&nbsp;  {{article.author.name}}</span>
                <span class="post-category">:&nbsp;  {{article.category.name}}</span>
                <span class="post-comment">:&nbsp;  {{article.comments.length}}</span>
                <span class="post-favorite">:&nbsp; {{article.meta.favourates}}</span>
                <router-link :to="{path:'/userArticle', query:{id:article._id}}"></router-link>
            </div>
        </div>

        <nav>
            <ul class="pagination">
                <li v-for="{n, index} in pages" :class="{'active':n==curPage}">
                </li>
            </ul>
        </nav>
    </div>
</template>
<script>
import moment from 'moment'
import truncate from 'truncate'

export default{
    data(){
        return{
            articles:[],
            pages:[],
            curPage:1,
            pageCount:0
        }
    },
    methods:{
        //
        getArticleList(n){
            this.$http.get('/article?page='+ n).then(function(res){
                this.articles = res.body.result;
                this.pageCount = res.body.pageCount;
                this.curPage = res.body.curPage;

                //the time&conent has formated
                for(let i=0; i<this.articles.length; i++){
                    this.articles[i].created=moment(this.articles[i].created).format('YYYY-MM-DD');
                    this.articles[i].content=truncate(this.articles[i].content, 140);
                }
            },function(res){
                alert(' '+ res.status);
            });
        }
    },

    created(){
        this.getArticleList(1);

        for(var i=0; i<this.pageCount; i++){
            this.pages.push(i+1);
        }
    }
}
</script>

<style scoped src="../css/blog.css">

</style>


