<template>
    <div>
        <h2 class="sub-header"> </h2>
        <form class="vertical-form" method="post">
            <div class="form-group">
                <label></label>
                <input class="form-control input-lg" type="text" v-model="title" placeholder="..." />
            </div>
            <div class="form-group">
                <label></label>
                <select class="form-control input-lg" v-model="category">
                    <option value="0">Javascript</option>
                    <option value="1">css</option>
                    <option value="2">html</option>
                    <option value="3">tools</option>
                </select>
            </div>
            <div class="form-group">
                <label></label>
                <textarea class="form-control" v-model="content"></textarea>
            </div>
            <a class="btn btn-primary" @click="add">{{submit}}</a>
        </form>
    </div>
</template>
<style>

</style>
<script>
    export default{
        data(){
            return{
                title:'',
                category:'',
                content:'',
                submit:'',
                id:''
            }
        },
        methods:{
            add(){
                //
                if(this.id!=''){
                    this.$http.put('/article/'+ this.id,{
                        title:this.title,
                        content: this.content,
                        author: 'admin',
                        category:this.category,
                        created: new Date
                    }).then(function(res){
                        if(res.status==200) console.log('');
                        window.location.href='adminArticleList';
                    },function(res){
                        console.log(' '+ res.status);
                    });
                //
                }else{
                    this.$http.post('/article',{
                        title:this.title,
                        content: this.content,
                        author: 'admin',
                        category:this.category,
                        created: new Date
                    }).then(function(res){
                        if(res.status==200) console.log('');
                        this.title='';
                        this.category='';
                        this.content='';
                    },function(res){
                        alert(' '+ res.status);
                    });
                }
            },

            //
            getModifyArticle(id){
                this.$http.get('/article/'+ id).then(function(res){
                    const article=eval('('+res.body+')');

                    this.title=article.title;
                    this.category=article.category;
                    this.content=article.content;
                },function(res){
                    alert(' '+ res.status);
                });
            }
        },
        created(){
            this.submit='';
            this.id=window.location.search.split('=')[1];
            if(this.id){
                this.submit='';
                this.getModifyArticle(this.id);
            }else{
                this.id='';
            }
        }
    }
</script>



